export default {
	data() {
		return {
			tempface: ''
		};
	},
	onLoad(params) {
		var tempFilePaths = params.tempFilePaths;
		this.tempface = tempFilePaths;
	},
	methods: {
		changePendingFace() {
			var me = this;
			// 重新选择头像
			uni.chooseImage({
				count: 1,
				sizeType: ['original', 'compressed'],
				sourceType: ['album'],
				success(res) {
					var tempFilePaths = res.tempFilePaths[0];
					me.tempface = tempFilePaths;
				}
			});
		},
		upload() {
			var me =this;
			var globalUser = me.getGlobalUser('globalUser');
			uni.showLoading({
				mask:true,
				title:"上传者请稍后"
			});
			uni.uploadFile({
				url:me.serverUrl+"//?userId="+globalUser.id,
				filePath:me.tempface,
				name:'file',
				header:{
					// headerUserId:globalUser.id,
					headerUserToken:globalUser.token
					
				},
				success(res){
					var resData=res.data;
					console.log(resData);
					
				},
				complete() {
					uni.hideLoading();
				}
				
			});
		}
	}
};
